.itemCard {
    height: 27em;
    width: 25.5em;
    margin-right: auto;
    margin-left: auto;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.137);
    border-radius: 10px;
    transition: 0.2s;
}

.itemCard:hover {
    border: 1.8px solid rgba(0, 0, 0, 0.363);
}

.productpic {
    width: 80%;
    height: 20em;
    margin-right: auto;
    margin-left: auto;
    margin: 1em auto 0px auto;
}

.productText {
    height: 15%;
    width: 80%;
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: center;
    margin-left: 2em;
    margin-top: 0.5em;
    padding-left: 10px;
}

.productName {
    font-family: monospace;
    font-size: 20px;
}

.productPrice {
    font-family: monospace;
    font-size: 15px;
    margin-top: 0.5em;
}

.art {
    max-width: 100%;
    max-height: 100%;
    transition: 0.3s;
    margin-top: 2px;

}

.art:hover {
    box-shadow: 5px 5px 5px rgba(68, 6, 6, 0.5);
    border: 1px solid black;
    cursor: pointer;
}

#addCart {
    color: rgba(114, 25, 25, 0.76);
    position: relative;
    bottom: 1.8em;
    left: 6em;
    font-size: 22px;
    z-index: 0;
}

#addCart:hover {
    color: rgba(3, 99, 3, 0.521);
    cursor: pointer;
}