.contactPage {
    width: 100%;
    height: 96vh;
    border: 1px solid red;
}

.contactInfo {
    font-family: monospace;
    width: 30%;
    margin: 33em auto 0px auto;
    transition: 1s;
}

.github {
    text-decoration: none;
    color: black;
    font-size: 20px;
}

.contactInfo:hover {
    background-color: rgba(0, 0, 0, 0.671);
    cursor: pointer;
}

.github:hover {
    color: white;
}