.cartDisplay {
    border: 1px solid white;
    box-sizing: border-box;
    width: 100%;
    z-index: -1;
}

.cartDiv {
    box-sizing: border-box;
    border: 2px solid black;
    width: 55%;
    margin: 6em auto 0px auto;
    height: 47em;
    background-image: url('./cartbacc.jpg');
    display: flex;
    align-items: center;
    justify-content: center;
}

.cartBackground {
    height: 95%;
    width: 90%;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.925);
}

.cartTitle {
    border-bottom: 1px solid rgb(235, 198, 198);
    box-sizing: border-box;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: monospace;
    font-size: 25px;
}

.cart {
    height: 33em;
    overflow-y: auto;
}


.cartButtons {
    box-sizing: border-box;
    border-top: 1px solid rgb(235, 198, 198);
    height: 4em;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.checkoutButton {
    width: 11em;
    height: 2em;
    border: none;
    cursor: pointer;
    border-radius: 8px;
    background-color: rgba(128, 128, 128, 0.13);
    transition: 0.5s;
    font-family: monospace;
    font-size: 15px;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    color: black
}

.checkoutButton:hover {
    background-color: rgba(128, 128, 128, 0.623);
}

.cartItem {
    box-sizing: border-box;
    border-bottom: 1px solid black;
    height: 7em;
    display: flex;
    align-items: center;
    justify-content: space-around;
    transition: 0.3s;
}

.cartItem:hover {
    background-color: rgba(255, 228, 196, 0.432);
}

.cartImg {
    height: 6em;
    width: 6em;
}

.cartText {
    height: 75%;
    width: 50%;
    
}

.cartItemTitle {
    height: 50%;
    width: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cartItemPrice {
    height: 50%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}


.cartItemButtons {
    width: 80px;
    display: flex;
    justify-content: center;
}

.quantity {
    width: 40px;
    background-color: rgba(255, 255, 255, 0);
    border: 1px solid black;
    transition: 0.3s;
}

.quantity:hover {
    background-color: rgba(0, 0, 0, 0.438);
    color: white;
    cursor: pointer;
}

.cartDelete {
    margin-top: 2px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0);
    border: 1px solid black;
    font-family: monospace;
    font-size: 12px;
    transition: 0.3s;
}

.cartDelete:hover {
    background-color: rgba(255, 0, 0, 0.24);
    cursor: pointer;
}

.cartItemImg {
    height: 100%;
    width: 100%;
}

.quantityInput {
    width: 20px;
    text-align: center;
}

.total {
    background-color: rgba(34, 18, 18, 0.514);
    height: 2.5em;
    display: flex;
    align-items: center;
    justify-content: right;
}

.orderTotal {
    justify-self: last baseline;
    margin-right: 15px;
    color: white;
    font-family: monospace;
    font-size: 15px;
}