.latin {
    font-family: 'Maven Pro', sans-serif;
    margin-top: 100px;
    width: 470px;
    margin-right: auto;
    margin-left: auto;
    font-size: 22px;
    text-align: left;
}

.shopPage {
    z-index: -1;
    padding-bottom: 80px;
}

.weirdpeople {
    width: 900px;
    margin-top: 200px;
}

.itemDisplay {
    margin-top: 100px;
    width: 90%;
    margin-right: auto;
    margin-left: auto;
    display: grid;
    grid-template-columns: 33% 33% 33%;
    grid-auto-rows: 400px;
    min-height: calc(100vh - 6rem);
    height: 100%;
    column-gap: 2em;
    row-gap: 7em;
}

@media screen and (max-width: 991px) {
    .itemDisplay {
        grid-template-columns: repeat(1,100%);
        column-gap: 5em;
    }
}

