.home {
    box-sizing: border-box;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    background-image: url('./artbacc.jpg');
    background-position: 93%;
    z-index: -1;
}

.homeButton {
    position: absolute;
    display: flex;
    width: 280px;
    height: 35px;
    border-radius: 10px;
    border: none;
    background-color: rgba(49, 47, 47, 0.432);
    color: white;
    top: 60%;
    right: 110px;
    font-size: 20px;
    font-family: monospace;
    transition: 0.5s;
    cursor: pointer;
    text-decoration: none;
    align-items: center;
    justify-content: center;
}

.homeButton:hover {
    background-color: rgba(49, 47, 47, 0.705);
}