.Navbar {
    width: 100vw;
    height: 7em;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    transition: 0.5s;
}

.Navbar:hover {
    background-color: white;
    z-index: 1;
}

.logo {
    width: 20%;
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 80px;
    font-family: 'Bebas Neue', cursive;
    margin-left: -20px;
    text-shadow: 4px 6px 2px rgba(0, 0, 0, 0.226);
    text-decoration: none;
    color: black;
    transition: 0.8s;
}

.logo:hover {
    text-shadow: 14px 16px 12px rgba(0, 0, 0, 0.226);
}

.linkBox {
    background-color: rgba(255, 255, 255, 0);
    display: flex;
    width: 85%;
    height: 70%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navBox {
    display: flex;
    gap: 50px;
    margin-left: 40%;
}

.contact {
    text-decoration: none;
    color: black;
    font-family: 'Maven Pro', sans-serif;
    font-size: 22px;
    transition: 0.3s;
}

.contact:hover {
    color: rgb(185, 18, 18);
}

.shop {
    text-decoration: none;
    color: black;
    font-family: 'Maven Pro', sans-serif;
    font-size: 22px;
    transition: 0.3s;
}

.shop:hover {
    color: rgb(185, 18, 18);
    outline: black;
}

#cartIcon {
    text-decoration: none;
    color: black;
    font-size: 30px;
    transition: 0.3s;
}

#cartIcon:hover {
    color: rgb(185, 18, 18);
}

.cartBox {
    display: flex;
    align-items: center;
}

.cartCount {
    border-radius: 20px;
    height: 15px;
    width: 15px;
    font-size: 12px;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-family: monospace;
    font-weight: bold;
    position: relative;
    right: 32%;
    top: 9px;
    text-decoration: none;
}

